import React, { useContext } from 'react';
import AnchorLink from 'react-anchor-link-smooth-scroll';
import { ThemeContext } from 'providers/ThemeProvider';
import { Header } from 'components/theme';
import { Container, Button } from 'components/common';
import dev from 'assets/illustrations/dev.svg';
import { Wrapper, IntroWrapper, Details, Thumbnail } from './styles';

export const Intro = () => {
  const { theme } = useContext(ThemeContext);

  const url = 'https://drive.google.com/file/d/1hXw8_3xwMJarp1j8wSxfm7Fh7q4KkqNA/view?usp=sharing'


  return (
    <Wrapper>
      <Header />
      <IntroWrapper as={Container}>
        <Details theme={theme}>
          <h1>Hi There!</h1>
          <h4>I’m Mahesh and I’m a Full Stack Developer!</h4>
          <Button as={AnchorLink} href="#contact">
            Contact Me
          </Button>
          
            <a href={url} target="_blank"><Button >Resume</Button></a>
          
        </Details>
        <Thumbnail>
          <img src={dev} alt="I’m Mahesh and I’m a Full Stack Developer!" />
        </Thumbnail>
      </IntroWrapper>
    </Wrapper>
  );
};
